.footer {
  color: white;
  padding-top: 26px;
  margin-top: 50px;
  background-color: rgb(23, 24, 26);
}

.firstpart {
  display: flex;
  justify-content: space-between;
  padding: 20px 1.3%;
}

.firstpart > div {
  display: flex;
  justify-content: space-evenly;
  gap: 40px;
  width: fit-content;
}

.heading {
  font-size: 17px;
  font-weight: 600;
  margin-bottom: 21.5px;
}

.firstpart > div.left > div > p:not(.heading) {
  margin: 16px 0px;
  color: rgb(212, 212, 212);
  font-size: 14px;
  font-weight: 600;
}

.firstpart > div.left {
  gap: 60px;
}

.rightheading {
  font-size: 25.5px;
  font-weight: 800;
}

.secondpart {
  display: flex;
  justify-content: space-between;
  background-color: rgb(32, 33, 35);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 15px 1.3%;
  font-size:12.3px;
  align-items: center;
}

.secondpart > img {
  height: 48px;
}

.socialicons {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin-top: 15px;
}

.socialicons > div {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  background-color: rgb(51, 52, 54);
  display: flex;
  align-items: center;
  justify-content: center;
}

.socialicons:nth-child(2) {
  justify-content: flex-start;
}

.stores {
  height: 30px;
}
